import {React, useEffect, useState} from 'react';
import heroImage from './Assets/backgroundCropped2.jpeg';
import './Styles/Main.css';
import Navbar from './Navbar';
import imi1 from './Assets/1.png';
import imi2 from './Assets/5.png';
import imi3 from './Assets/3.png';
import Footer from './Footer';

const Main = () => {
  
  return (
    <div className="main-content">
        <Navbar/>
          <div className="hero-section">
        <img src={heroImage} alt="Hero" className="hero-image" />
      </div>
      <div className="hero-text">
  <h4>Truhlstvm za tstm: devo, kter zanech tsku ve vaem srdci.</h4>
  <p>
  Na mru vytvoen skn, kter dokonale zapadnou do vaeho domova a spln vechna vae pn. Kad kus je peliv navren s ohledem na v prostor, styl a funknost, abyste zskali maximln lon prostor i estetick zitek. Nae skn spojuj kvalitn emeslnou prci s modernm designem, co vm zaruuje dlouhou ivotnost a spokojenost. Nechte si od ns vytvoit sk, kter se stane neodmyslitelnou soust vaeho kadodennho ivota.
  </p>
</div>


{/* navbar */}
<div class="secondary-navbar">
  <ul>
    <li><a href="/onas">O Ns</a></li>
    <li><a href="/projekty">Projekty</a></li>
    <li><a href="/kontakt">Kontakt</a></li>
  </ul>
</div>



          {/* Ukzky projekt */}
      <div className="featured-content">
        <div className="featured-item">
          <img src={imi1} alt="Feature 1" className="featured-image" />
          <h4>Elegantn, modern kuchyn na mru, s preciznm designem v ernm matnm kameni s devnm prvkem</h4>
        </div>
        <div className="featured-item">
          <img src={imi2} alt="Feature 2" className="featured-image" />
          <h4>Skn pro njakho zkurvenho prvnka, co si mus podit pokoj skn, aby mla jeho ena kam dvat boty</h4>
        </div>
        <div className="featured-item">
          <img src={imi3} alt="Feature 3" className="featured-image" />
          <h4>Tohle se d koupit v ikee za plku penz.....</h4>
        </div>
      </div>
      <a className="news-tag" href="/projekty" >Dal projekty</a>


<div class="contact-container">
    <a href="/kontakt" class="contact-button">
        Kontaktujte ns
    </a>
</div>


<Footer/>
    </div>
  );
};

export default Main;
