// Navbar.js
import React from 'react';
import Navbar from './Navbar';
import Footer from './Footer';
import './Styles/Dilna.css';
import imi1 from './Assets/1.png';
import imi2 from './Assets/van.png';
import imi3 from './Assets/3.png';

const Dilna = () => {
        return (
           <div><Navbar/>
           <div className="three-rows-container">
      {/* Row 1: Image on the left, text on the right */}
      <div className="row">
        <div className="image-container">
          <img src={imi1} alt="Row 1 Image" />
        </div>
        <div className="text-container">
          <h2>Tady bude fotka Filipa, jak si hon pro na lepice nebo jak se t chujovin ik</h2>
          <p>A tady srdceryvn strka o tom, jak si krsn vyhonil a nackal to nkomu do skn.</p>
        </div>
      </div>

      {/* Row 2: Image on the right, text on the left */}
      <div className="row reverse">
        <div className="image-container">
          <img src={imi2} alt="Row 2 Image" />
        </div>
        <div className="text-container">
          <h2>Tady bude fotka va lut krsky.</h2>
          <p>Pidal bych do fotky i Lipiho jak si hon pro a lk do dodvky dti na bomboy.</p>
        </div>
      </div>

      {/* Row 3: Image on the left, text on the right */}
      <div className="row">
        <div className="image-container">
          <img src={imi3} alt="Row 3 Image" />
        </div>
        <div className="text-container">
          <h2>A tady asi kontakt pro pidn se do tmu.</h2>
          <p>Protoe jestli nkdo zvldne ty prvn dv fotky a okamit nevypne pota, aby si prostelil hlavu, tak je to stejnej uchyl jako vy a mete ho rovnou zamstnat.</p>
        </div>
      </div>
    </div>
           
           <Footer/></div>
      );
    };
    
    export default Dilna;
    
